<template>
	<div id="edit-employ">
		<h3>Edit Employee</h3>
		<div class="row">
			<form @submit.prevent="updateEmploy" class="col s12">
				<div class="row">
					<div class="input-field col s12">
						<input disabled="" type="text" v-model="emp_id" required="">
					</div>
				</div>
				<div class="row">
					<div class="input-field col s12">
						<input type="text" v-model="emp_name" required="">
					</div>
				</div>
				<div class="row">
					<div class="input-field col s12">
						<input type="text" v-model="dept" required="">
					</div>
				</div>
				<router-link to="/" class="btn grey">Cancel</router-link>
				<button type="submit" class="btn">Update</button>
			</form>
		</div>
	</div>
</template>
<script>
import db from './firebaseinit'
export	default {
	name: 'edit-employ',
	data() {
		return {
			emp_id: null,
			emp_name: null,
			dept: null
		}
	},
	beforeRouteEnter (to, from, next) {
		db.collection('employees').where('emp_id', '==', to.params.emp_id).get()
		.then(querySnapshot => {
			querySnapshot.forEach(doc => {
				next(vm => {
					vm.emp_id = doc.data().emp_id,
					vm.emp_name = doc.data().emp_name,
					vm.dept = doc.data().dept
				})
			})
		})
	},
	watch: {
		'$route': 'fetchData'
	},
	methods: {
		fetchData() {
			db.collection('employees').where('emp_id', '==', this.$route.params.emp_id) .get().then(querySnapshot => {
				querySnapshot.forEach(doc => {
					this.emp_id = doc.data().emp_id,
					this.emp_name = doc.data().emp_name,
					this.dept = doc.data().dept
				})
			})
		},
		updateEmploy() {
			db.collection('employees').where('emp_id', '==', this.$route.params.emp_id) .get().then(querySnapshot => {
				querySnapshot.forEach(doc => {
					doc.ref.update({
						emp_id: this.emp_id,
						emp_name: this.emp_name,
						dept: this.dept,
					})
					.then(() => {
						this.$router.push({name: 'view-employ', params: {emp_id: this.emp_id}})
					})
				})
			})
		}
	}
}
</script>


