<template>
	<div id="view-employ">
		<ul class="collection with-header">
			<li class="collection-header">
				<h4>{{emp_name}}</h4>
			</li>
			<li class="collection-item">Employ ID: {{emp_id}}</li>
			<li class="collection-item">Employ Dept.: {{dept}}</li>
		</ul>
		<router-link to="/" class="btn grey">Back</router-link>
		<button @click="deleteEmploy" class="btn red">Delete</button>

		<div class="fixed-action-btn">
			<router-link v-bind:to="{name: 'edit-employ', params: {emp_id: emp_id}}" class="btn-floating btn-large red">
				<i class="fa fa-pencil"></i>
			</router-link>
		</div>
	</div>
</template>
<script>
import db from './firebaseinit'
export	default {
	name: 'view-employ',
	data() {
		return {
			emp_id: null,
			emp_name: null,
			dept: null
		}
	},
	beforeRouteEnter (to, from, next) {
		db.collection('employees').where('emp_id', '==', to.params.emp_id).get()
		.then(querySnapshot => {
			querySnapshot.forEach(doc => {
				next(vm => {
					vm.emp_id = doc.data().emp_id,
					vm.emp_name = doc.data().emp_name,
					vm.dept = doc.data().dept
				})
			})
		})
	},
	watch: {
		'$route': 'fetchData'
	},
	methods: {
		fetchData() {
			db.collection('employees').where('emp_id', '==', this.$route.params.emp_id) .get().then(querySnapshot => {
				querySnapshot.forEach(doc => {
					this.emp_id = doc.data().emp_id,
					this.emp_name = doc.data().emp_name,
					this.dept = doc.data().dept
				})
			})
		},
		deleteEmploy() {
		  if(confirm('Are you sure?')) {
			db.collection('employees').where('emp_id', '==', this.$route.params.emp_id) .get().then(querySnapshot => {
				querySnapshot.forEach(doc => {
					doc.ref.delete()
					this.$router.push('/')
				})
			})
		  }
		}
	}
}
</script>


