import Vue from 'vue'
import Router from 'vue-router'
import dashboard from '@/components/dashboard'
import newEmploy from '@/components/newEmploy'
import viewEmploy from '@/components/viewEmploy'
import editEmploy from '@/components/editEmploy'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: dashboard
    },
    {
      path: '/new',
      name: 'new-employ',
      component: newEmploy
    },
    {
      path: '/:emp_id',
      name: 'view-employ',
      component: viewEmploy
    },
    {
      path: '/edit/:emp_id',
      name: 'edit-employ',
      component: editEmploy
    }
  ]
})



