<template>
	<div id="new-employ">
		<h3>New Employee</h3>
		<div class="row">
			<form @submit.prevent="saveEmploy" class="col s12">
				<div class="row">
					<div class="input-field col s12">
						<input type="text" v-model="emp_id" required="">
						<label>Employ ID</label>
					</div>
				</div>
				<div class="row">
					<div class="input-field col s12">
						<input type="text" v-model="emp_name" required="">
						<label>Employ Name</label>
					</div>
				</div>
				<div class="row">
					<div class="input-field col s12">
						<input type="text" v-model="dept" required="">
						<label>Department</label>
					</div>
				</div>
				<router-link to="/" class="btn grey">Cancel</router-link>
				<button type="submit" class="btn">Submit</button>
			</form>
		</div>
	</div>
</template>
<script>
import db from './firebaseinit'
export	default {
	name: 'new-employ',
	data() {
		return {
			emp_id: null,
			emp_name: null,
			dept: null
		}
	},
	methods: {
		saveEmploy() {
			db.collection('employees').add({
				emp_id: this.emp_id,
				emp_name: this.emp_name,
				dept: this.dept
			})
			.then(docRef => this.$router.push('/'))
			.catch(error => console.log(err))
		}
	}
}
</script>


